import React, { Component, Fragment } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import Localization from '../../context/localization';
import * as sessionSelectors from '../../redux/session/sessionSelectors';
import Header from '../../components/header/header';
import IndexPage from '../indexPage/indexPage';
import Footer from '../../components/footer/footer';

class StartPage extends Component {
  state = {};

  render() {
    const {
      header,
      quickLinks,
      localizesObj,
      viber,
      office,
      viberMob,
      newsIndex,
      routes,
      map,
      footer,
    } = this.props;
    const { locale } = localizesObj;
    return (
      <Fragment>
        <Header header={header} />
        <main>
          <Route
            exact
            path={['/uk/', '/ru/']}
            render={() => (
              <IndexPage
                viber={viber}
                quickLinks={quickLinks}
                office={office}
                viberMob={viberMob}
                newsIndex={newsIndex}
                routes={routes}
                map={map}
              />
            )}
          />
        </main>
        <Footer {...footer} />
      </Fragment>
    );
  }
}

const mapStateToProps = state => ({
  locale: sessionSelectors.locale(state),
});

function LocalizedStartPage(localizesObj) {
  return (
    <Localization.Consumer>
      {({ localizedContent, currentLocale }) => (
        <StartPage
          mapStateToProps
          localizesObj={localizesObj}
          {...localizedContent}
          currentLocale={currentLocale}
        />
      )}
    </Localization.Consumer>
  );
}

export default connect(mapStateToProps)(LocalizedStartPage);
