import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import { TextField } from '@material-ui/core';
import * as sessionOperations from '../../../redux/session/sessionOperations';
import style from './regForm.module.css';
import loginErrorImg from '../../../assets/img/icons/errorImage.svg';

const regExp =
  "^[-a-z0-9!#$%&'*+/=?^_`{|}~]+(?:.[-a-z0-9!#$%&'*+/=?^_`{|}~]+)*@(?:[a-z0-9]([-a-z0-9]{0,61}[a-z0-9])?.)*(?:aero|arpa|asia|biz|cat|com|coop|edu|gov|info|int|jobs|mil|mobi|museum|name|net|org|pro|tel|travel|[a-z][a-z])$";

const CssTextField = withStyles({
  root: {
    fontFamily: 'Open sans',
    fontSize: 16,
    lineHeight: 22,
    color: '#FBFBFB',
    '&:nth-child(2)': {
      marginBottom: 25,
    },
    '& .MuiInput-input': {
      backgroundRepeat: 'no-repeat',
      color: '#FBFBFB',
      padding: '6px 10px 7px 10px',
    },
    '& label': {
      fontSize: 12,
      color: '#FBFBFB',
      marginLeft: 10,
      whiteSpace: 'nowrap',
    },
    '& label.Mui-focused': {
      color: '#FBFBFB',
    },
    '&:hover .MuiInput-underline:before': {
      borderBottomColor: '#315F8F',
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: '#315F8F',
    },
    '& .MuiOutlinedInput-root': {
      '&.Mui-focused fieldset': {
        borderColor: '#315F8F',
      },
    },
    '& .MuiInput-underline::before': {
      borderBottom: '1px solid #315F8F',
    },
  },
})(TextField);

class RegForm extends Component {
  state = {
    mail: '',
    password: '',
  };

  handleChange = ({ target }) => {
    const { name, value } = target;

    this.setState({ [name]: value });
  };

  handleSubmit = e => {
    e.preventDefault();

    this.props.onLogin({ ...this.state });
  };

  render() {
    const { mail, password } = this.state;
    const { textFields, setErrorNull, loginErrorLK } = this.props;

    const {
      email,
      enterPassword,
      forgot,
      instruction,
      buttonEnter,
      buttonRegistration,
    } = textFields;

    return (
      <form
        onClick={setErrorNull}
        onSubmit={this.handleSubmit}
        className={style.form}
      >
        <CssTextField
          onChange={this.handleChange}
          name="mail"
          value={mail}
          type="email"
          id="standard-input"
          label={email}
          required
          inputProps={{
            pattern: regExp,
          }}
        />
        <CssTextField
          onChange={this.handleChange}
          name="password"
          value={password}
          type="password"
          id="standard-password"
          label={enterPassword}
          inputProps={{ minLength: '6', maxLength: '25' }}
          required
        />
        <div className={style.buttonWrapper}>
          <button className={style.button} type="submit">
            {buttonEnter}
          </button>
          <a
            href="https://cpp.infoxvod.com.ua/#REGISTER"
            target="_blank"
            rel="noopener noreferrer"
            className={style.linkButton}
          >
            {buttonRegistration}
          </a>
          {loginErrorLK && (
            <div onClick={setErrorNull} className={style.errorWrapper}>
              <img
                className={style.errorIcon}
                src={loginErrorImg}
                alt="error icon"
              />
              <p className={style.errorText}>
                {(loginErrorLK.data && loginErrorLK.data.text) ||
                  'Some network error'}
              </p>
            </div>
          )}
        </div>
        <a
          href="https://www.google.com/"
          target="_blank"
          rel="noopener noreferrer"
          className={style.help}
        >
          {forgot}
        </a>
        <a
          href="https://www.google.com/"
          target="_blank"
          rel="noopener noreferrer"
          className={style.help}
        >
          {instruction}
        </a>
      </form>
    );
  }
}

const mapDispatchToProps = {
  onLogin: sessionOperations.login,
};

export default connect(null, mapDispatchToProps)(RegForm);
