import PropTypes from 'prop-types';
import React, { Component } from 'react';
import style from './mapInfo.module.css';
import searchIcon from '../../../assets/img/icons/search.svg';
import MotionComponent from './motionComponent/motionComponent';
import placeholderImg from '../../../assets/img/icons/icon-placeholder.svg';

const hour = time => {
  const date = new Date(Date.parse(time));
  return date;
};

const percentStyles = {
  position: 'absolute',
  zIndex: 2,
  top: 0,
  left: 0,
  height: '100%',
  background: '#0B8EC5',
  clipPath: 'polygon(4px 0, 100% 0%, calc(100% - 4px) 100%, 0 100%)',
};

const timePercent = (timeStart, timeEnd) => {
  const timeFull = Date.parse(timeEnd) - Date.parse(timeStart);
  const timeCollapsed = Date.now() - Date.parse(timeStart);

  return Math.round((timeCollapsed / timeFull) * 100) <= 100
    ? Math.round((timeCollapsed / timeFull) * 100)
    : 100;
};

function currentCrushChange(crushes, value) {
  const currentCrush = crushes.filter(
    crush =>
      crush.address.toLowerCase().includes(value.toLowerCase()) ||
      crush.places.find(el => el.toLowerCase().includes(value.toLowerCase())),
  );
  return currentCrush;
}

export default class MapInfo extends Component {
  state = {
    searchValue: '',
  };

  handleChange = ({ target }) => this.setState({ searchValue: target.value });

  render() {
    const { currentId, dataMap, handleClick, search, mapLoading } = this.props;
    const { searchValue } = this.state;
    const currentCrushes =
      searchValue === '' ? dataMap : currentCrushChange(dataMap, searchValue);
    return (
      <div className={style.wrapper}>
        <div className={style.form}>
          <input
            className={style.input}
            onChange={this.handleChange}
            type="text"
            name="search"
            placeholder={search}
            value={searchValue}
          />
          <div className={style.searchImgWrapper}>
            <img
              className={style.searchImg}
              alt="search icon"
              src={searchIcon}
            />
          </div>
        </div>
        <ul className={style.list}>
          {currentCrushes.length > 0
            ? currentCrushes.map(crush => (
                <li
                  onClick={() => handleClick(crush.repair_id)}
                  key={crush.repair_id}
                  className={style.listItem}
                >
                  <p className={style.streetName}>
                    <img
                      src={placeholderImg}
                      alt="street placeholder"
                      className={style.placeholderImg}
                    />
                    <span>{crush.address}</span>
                  </p>
                  <p className={style.crushName}>{crush.atext}</p>
                  <div className={style.dateWrapper}>
                    <p className={style.timeStart}>
                      <span className={style.hours}>
                        {hour(crush.sf_time).getHours() >= 10
                          ? hour(crush.sf_time).getHours()
                          : `0${hour(crush.sf_time).getHours()}`}
                        .
                        {hour(crush.sf_time).getMinutes() >= 10
                          ? hour(crush.sf_time).getMinutes()
                          : `0${hour(crush.sf_time).getMinutes()}`}
                      </span>
                      <span className={style.date}>
                        {hour(crush.sf_time).getUTCDate() > 8
                          ? hour(crush.sf_time).getUTCDate() + 1
                          : `0${hour(crush.sf_time).getUTCDate() + 1}`}
                        .
                        {hour(crush.sf_time).getUTCMonth() > 8
                          ? hour(crush.sf_time).getUTCMonth() + 1
                          : `0${hour(crush.sf_time).getUTCMonth() + 1}`}
                        .{hour(crush.sf_time).getUTCFullYear()}
                      </span>
                    </p>
                    <p className={style.timeEnd}>
                      <span className={style.hours}>
                        {hour(crush.rp_time).getHours() >= 10
                          ? hour(crush.rp_time).getHours()
                          : `0${hour(crush.rp_time).getHours()}`}
                        .
                        {hour(crush.rp_time).getMinutes() >= 10
                          ? hour(crush.rp_time).getMinutes()
                          : `0${hour(crush.rp_time).getMinutes()}`}
                      </span>
                      <span className={style.date}>
                        {hour(crush.rp_time).getUTCDate() > 8
                          ? hour(crush.rp_time).getUTCDate() + 1
                          : `0${hour(crush.rp_time).getUTCDate() + 1}`}
                        .
                        {hour(crush.rp_time).getUTCMonth() > 8
                          ? hour(crush.rp_time).getUTCMonth() + 1
                          : `0${hour(crush.rp_time).getUTCMonth() + 1}`}
                        .{hour(crush.rp_time).getUTCFullYear()}
                      </span>
                    </p>
                  </div>
                  <div className={style.timeDiagram}>
                    <span className={style.timePercent}>
                      {timePercent(crush.sf_time, crush.rp_time)}%
                    </span>
                    <div
                      style={{
                        ...percentStyles,
                        width: `${timePercent(crush.sf_time, crush.rp_time)}%`,
                      }}
                    />
                  </div>
                  <MotionComponent currentId={currentId} crush={crush} />
                </li>
              ))
            : !mapLoading && (
                <p className={style.noRezults}>No matching results!</p>
              )}
        </ul>
      </div>
    );
  }
}

MapInfo.defaultProps = {
  dataMap: [
    {
      repair_id: '',
      address: 'some street',
      atext: 'some crush',
      sf_time: '2019-11-24T06:10',
      rp_time: '2019-11-24T15:00',
      type: 'v',
      places: ['some streets'],
      location: {
        lat: '46.479787',
        lng: '30.737273',
      },
    },
  ],
};

MapInfo.propTypes = {
  dataMap: PropTypes.arrayOf(
    PropTypes.shape({
      repair_id: PropTypes.string,
      address: PropTypes.string,
      atext: PropTypes.string,
      sf_time: PropTypes.string,
      rp_time: PropTypes.string,
      type: PropTypes.string,
      places: PropTypes.arrayOf(PropTypes.string),
      location: PropTypes.shape({
        lat: PropTypes.string,
        lng: PropTypes.string,
      }),
    }),
  ),
};
