import axios from 'axios';
import {
  initDataRequest,
  initDataSuccessUK,
  initDataSuccessRU,
  initDataVidzhetySuccessUK,
  initDataVidzhetySuccessRU,
  initDataErrorUK,
  initDataErrorRU,
} from './initDataActions';
import { locale } from '../session/sessionSelectors';

const setAxiosLang = lang => {
  axios.defaults.headers.common['Accept-Language'] = lang;
};

// eslint-disable-next-line import/prefer-default-export
export const onInitDataOperation = () => (dispatch, getState) => {
  axios.defaults.baseURL = 'https://dev2.cybercreation.team/api/menu';
  dispatch(initDataRequest());

  setAxiosLang(locale(getState()));

  axios
    .get()
    .then(response => {
      if (locale(getState()) === 'uk') {
        try {
          dispatch(initDataVidzhetySuccessUK(response.data.vidzhety));
          dispatch(initDataSuccessUK(response.data.gamburger));
        } catch (error) {
          dispatch(initDataErrorUK(error));
        }
      } else {
        try {
          dispatch(initDataVidzhetySuccessRU(response.data.vidzhety));
          dispatch(initDataSuccessRU(response.data.gamburger));
        } catch (error) {
          dispatch(initDataErrorRU(error));
        }
      }
    })
    .catch(error => {
      console.log(error);
      if (locale(getState()) === 'uk') dispatch(initDataErrorUK(error));
      else {
        dispatch(initDataErrorRU(error));
      }
    });
};
