import React from 'react';
import { compose, withProps } from 'recompose';
import PropTypes from 'prop-types';
import {
  withScriptjs,
  withGoogleMap,
  GoogleMap,
  Marker,
} from 'react-google-maps';
import coldWater from '../../assets/img/icons/icon_cold water.svg';
import canalization from '../../assets/img/icons/icon_canalisation.svg';

const iconPath = type => {
  return type === 'v' ? coldWater : canalization;
};

const MyMapComponent = compose(
  withProps({
    googleMapURL:
      'https://maps.googleapis.com/maps/api/js?&v=3.exp&libraries=geometry,drawing,places',
    loadingElement: (
      <div
        style={{
          height: `100%`,
        }}
      />
    ),
    containerElement: (
      <div
        style={{
          height: `100%`,
        }}
      />
    ),
    mapElement: (
      <div
        style={{
          height: `100%`,
        }}
      />
    ),
  }),
  withScriptjs,
  withGoogleMap,
)(props => {
  const { currentLat, currentLng, markerList } = props;
  return (
    <GoogleMap
      defaultZoom={13}
      defaultCenter={{
        lat: 46.964691,
        lng: 31.983631,
      }}
      center={{
        lat: Number(currentLat),
        lng: Number(currentLng),
      }}
    >
      {markerList.map(marker => (
        <Marker
          key={marker.repair_id}
          position={{
            lat: Number(marker.location.lat),
            lng: Number(marker.location.lng),
          }}
          icon={{
            url: iconPath(marker.type),
            scaledSize: new window.google.maps.Size(50, 50),
          }}
          onClick={() => props.handleClick(marker.repair_id)}
        />
      ))}
    </GoogleMap>
  );
});

